import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { WishlistService } from 'src/app/service/wishlist.service';
declare var swal: any;

@Component({
  selector: 'app-wishlist-add',
  templateUrl: './wishlist-add.component.html',
})
export class WishlistAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;

  productList: any = [];
  productImage='';
  showLoader = false;
  formErrors = {
    product_id: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private wishlistservice: WishlistService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      product_id: [null],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.wishlistservice.getWishlistList(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.addForm.patchValue({
          product_id: response.data.product_id,
        });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/wishlist');
    });
  }


  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.wishlistservice.editWishlist(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/wishlist');
          } else {
            this.showLoader = false;
            response.error.map(obj => {
              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            });
          }
        });
      } else {
        this.wishlistservice.addWishlist(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/wishlist');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            });
          }
        });
      }
    }
  }
}
